import './App.css';
import { Routes, Route } from 'react-router-dom';
import { useState } from 'react';
import axios from 'axios';
import SearchBar from './components/SearchBar/SearchBar';
import Landing from './components/Landing/Landing';
import Pokemones from './components/Pokemones/Pokemones'

function App() {

  const [characters, setCharacters] = useState([]);

  const onSearch = async (name) => {
    try{
      const { data } = await axios(`https://pokeapi.co/api/v2/pokemon/${name}`)
        if (data.id) {
            setCharacters((oldChars) => [...oldChars, data]);
            alert('Se encontro el pokemon')
        } 
    }
    catch(error){
      alert('¡No hay pokemons con este nombre!');
    }
 }


  return (
    <div className="container">
      <SearchBar onSearch={onSearch}/>
      <div>
        <Routes>
        <Route path='/' element={<Landing/>}/>
        <Route path='/home' element={<Pokemones characters={characters}/>}/>
        </Routes>
        </div>
    </div>
  );
}

export default App;
