import Pokemon from '../Pokemon/Pokemon'

export default function Pokemones (charcaters){
    return(
        <div>
            {charcaters.map(({name, sprites, types}) =>{
                return(
                    <Pokemon
                    name={name}
                    sprites={sprites}
                    types={types}
                    />
                )
            })}
        </div>
    )
}